.site-container{
    height: 100%;
    min-height: 100%;
    position: relative;
    margin: 0px auto;

    box-sizing: border-box;
    overflow-y: auto;
    overflow-x: hidden;
    max-width:100%;
    width:100%;
}
/*//background-color: rgba(0,0,0,0.3);*/
.site-container-no-scroll{ /** used by game post */
    height:100%; 
    position: relative; 
    margin:0px;
}



