*, element {
    --color-ppD: rgb(28, 8, 87);
    --color-ppL: rgb(58, 32, 136);
    --color-neutralL: #1F2121;
    --color-neutralD: black;
}
.bg-text{
  background-color: rgba(0,0,0,0.5)
}
.bg-grad-pp{
  border-top: 1px solid var(--color-ppL);
  border-bottom: 3px solid var(--color-ppL);
  background-image: linear-gradient(var(--color-neutralD) 40%, var(--color-ppD) 100%);
}
.bg-grad-pk{
  background-image: radial-gradient(black 60%, rgb(65, 63, 64) 170%);
}
.bg-grad-lbl{
  background-image: linear-gradient( rgb(66, 3, 82), black 60%, black 70%, rgb(66, 3, 82) 110%);
  border-top: 3px solid rgb(115, 31, 136);
  border-bottom: 1px solid rgb(115, 31, 136);
}
