@keyframes animateInAbsolute{
    from{opacity: 0; transform: translateY(-100%)}
    to{opacity:1; transform: translateY(-50%)}
}
@keyframes slideDown{
  from{opacity: 0; transform: translateY(-100%)}
  to{opacity:1; transform: translateY(0%)}
}
@keyframes slideUp{
  from{opacity: 0; transform: translateY(100%)}
  to{opacity:1; transform: translateY(0%)}
}
@keyframes fadeIn{
    from{opacity: 0}
    to{opacity:1}
}
.anim-fadeIn{
  opacity: 0;
  animation: fadeIn 0.5s ease-in forwards;
}
.jump{
    animation: jump 1s ease-in infinite forwards;
}
.anim-pulse{
  animation: pulse 3s ease-in infinite forwards;
}
.anim-pulse-trans{
  animation: pulse-trans 3s ease-in infinite forwards;
}
@keyframes jump{
    0% {transform: translateY(0%)}
    60% {transform: translateY(0%)}
    75% {transform: translateY(-25%)}
    80% {transform: translateY(-25%)}
    95% {transform: translateY(0%)}
    100% {transform: translateY(0%)}
}
@keyframes pulse{
  0% {transform: scale(1)}
  60% {transform: scale(1)}
  75% {transform: scale(1.25)}
  80% {transform: scale(1.25)}
  95% {transform: scale(1)}
  100% {transform: scale(1)}
}
@keyframes pulse-trans{
  0% {transform: scale(1) translateY(-50%)}
  73% {transform: scale(1) translateY(-50%)}
  78% {transform: scale(1.25) translateY(-50%)}
  80% {transform: scale(1.25) translateY(-50%)}
  85% {transform: scale(1) translateY(-50%)}
  100% {transform: scale(1) translateY(-50%)}
}

@media(hover: hover){
  .hoverable{
    opacity: 0.7;
  }
  .hoverable:hover{
    opacity:1;
  }
  .active .hoverable, .active.hoverable{
    opacity: 1;
  }
}
.hoverable{
  transition: transform 0.3s ease, opacity 0.3s ease;
}
.hoverable::selection{
  transform: scale(1.2);
}
.hoverable::-moz-selection{
  transform: scale(1.2);
}


