/* Customize the label (the container) */
.radio-item{
    color: white;
    padding-left: 30px;
}
.radio{
    width: 80%;
    max-width: 600px;
    text-align: left;
    margin: auto;
    padding: 10px;
}
.container {
    display: block;
    position: relative;
    margin-right: 10px;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }
  .container label{
      display: inline-block;
      position: relative;
  }
  /* Hide the browser's default radio button */
  .container label input {
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
  }
  
  /* Create a custom radio button */
  .checkmark {
    position: absolute;
    top: 3px;
    left: 0px;
    height: 20px;
    width: 20px;
    background-color: #EDE32E;
    opacity: 0.3;
    border-radius: 50%;
    transition: opacity 0.3s ease;
  }
  
  /* On mouse-over, add a grey background color */
 /* .checkmark:hover  {
    background-color: red;
  } */
  .container:hover label input ~ .checkmark{
    background-color: #EDE32E;
    opacity: 0.7;
  }
  
  
  /* When the radio button is checked, add a blue background */
  .container label input:checked ~ .checkmark {
    background-color: #EDE32E;
    opacity: 1;
  }
  
  /* Create the indicator (the dot/circle - hidden when not checked) */
  .checkmark:after {
    content: "";
    position: absolute;
    display: none;
  }
  
  /* Show the indicator (dot/circle) when checked */
  .container label input:checked ~ .checkmark:after {
    display: block;
  }
  
  /* Style the indicator (dot/circle) */
  .container label .checkmark:after {
    top: 30%;
    left: 30%;
    width: 40%;
    height: 40%;
    border-radius: 50%;
    background: transparent;

  }
/**     animation: scaleDown 1s ease forwards;*/
  @keyframes scaleDown{
      from{
          transform: scale(0.1)
      }
      to{
          transform: scale(1.2);
      }
  }