.text-capitalize{
  text-transform: capitalize;
}
.text-left{
  text-align: left;
}
.text-center{
  text-align: center;
}
.text-right{
  text-align: right;
}
.text-float-left{
  float: left;
}
.text-float-right{
  float:right;
}
.text-bold{
  font-weight: bold;
}
.text-italic{
  font-style:italic;
}
.text-underline{
  text-decoration: underline;
}
.text-decoration-none{
  text-decoration: none;
}
.text-whitespace-preserve{
  white-space: pre;
}
.text-nowrap{
  white-space: nowrap;
}
.text-overflow-hidden{
  text-overflow: hidden;
}
.text-overflow-ellipses{
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.text-hover{
  transition: opacity 0.3s ease;
  opacity: 0.8;
}
.text-hover:hover{
  opacity: 1;
}

