@-webkit-keyframes glowO {
  0% {
    opacity: 0.2;
    transform:scale(0.999, 1.05);
  }
  20% {
     opacity: 0.4;
     transform:scale(1, 1);
  }
  40% {
      opacity: 0.6;
      transform: scale(0.98, 1.1);
   }
   60% {
      opacity: 0.3;
      transform: scale(0.99, 0.98);
   }
   80% {
      opacity: 0.5;
      transform: scale(0.99, 1.2);
   }
}